import type { ReactiveController, ReactiveElement } from 'lit';
import type { OmniElement } from '../omni-element.js';

export interface OpenableElement extends ReactiveElement {
  opened: boolean;
}
/**
 * - if your element has an 'opened' prop, it will automatically set to 'false' on outside click
 * - else, provide a function to call for closing
 */
export class ClickOutsideController implements ReactiveController {
  constructor(
    private host: OpenableElement | OmniElement,
    private closeFn: (e: PointerEvent) => void = null
  ) {
    this.host.addController(this);
  }

  hostConnected() {
    document.addEventListener('click', this.#handleClickOutside as EventListener);
  }

  hostDisconnected() {
    document.removeEventListener('click', this.#handleClickOutside as EventListener);
  }

  #handleClickOutside = (e: PointerEvent) => {
    if (!e.composedPath().includes(this.host)) {
      if ('opened' in this.host && !this.closeFn) {
        (this.host as OpenableElement).opened = false;
      } else if (this.closeFn) {
        this.closeFn(e);
      } else {
        // no action
      }
    }
  };
}
