// note that the @internal tag is used to remove the function from the generated documentation in storybook
// `@internal` functions can be used if you understand what they do and how to use them

function mergekeyvalue(to, [key, value]) {
  const orig = to[key];
  if (orig && typeof orig === 'object' && typeof value === 'object') {
    merger(orig, value);
  } else {
    to[key] = value;
  }
  return to;
}

function merger(to, from) {
  if (Array.isArray(to) && Array.isArray(from)) {
    to.push(...from);
  } else if (from) {
    let source;
    try {
      /*
       * [Note] protection against unsupported data types
       * For example structuredClone only supports plain objects
       * objects created from the FormData constructor cannot be cloned
       */
      source = structuredClone(from);
    } catch (e) {
      source = from;
    }

    Object.entries(source).reduce(mergekeyvalue, to);
  }
  return to;
}

/*
 * simplistic deep merge of objects with shallow merge of arrays, ignores null and undefined secondary
 * returns a new object without modifying the original objects
 * @param   {Object} to	           the target object
 * @param   {Array} from	         additional object arguments
 * @returns {Object}               merged object
 * @example
 *	merge([1,2], [3,4])
 *	// =>	[1,2,3,4]
 *	merge(
 *		{a:'1st', b:{b:0, sub:{m:[22]}}, list: [7,8]},
 *		{a:'2nd', b:{c:0, sub:{m:[33]}}, list: [2,3], done: true},
 *		{r: 2}
 *	)
 *	// =>	{a:'2nd', b: {b:0, c:0, sub:{m:[22,33]}}, list: [7,8,2,3], done: true, r: 2}
 *	merge({a:{b:1}}, {}, undefined, null, {a:{c:2}})
 *	// =>	{a:{b:1,c:2}}
 * */
export function merge(to, ...from) {
  const copyTarget = structuredClone(to);
  return from.reduce(merger, copyTarget ?? {});
}

const baseDateFormat = { month: 'short', day: 'numeric', year: 'numeric' };
const baseTimeFormat = { hour: 'numeric', minute: 'numeric' };
const baseRelTimeFormatOptions = { numeric: 'auto' };
const timeUnits = {
  year: 24 * 60 * 60 * 1000 * 365,
  month: (24 * 60 * 60 * 1000 * 365) / 12,
  day: 24 * 60 * 60 * 1000,
  hour: 60 * 60 * 1000,
  minute: 60 * 1000,
  second: 1000
};

/**
 * Omni standard for displaying localized dates
 *
 * @param      {Date}   date                      a Date object
 * @param      {Object} [formatOverides]          any overrides to base display format
 * @returns    {String}                           the date string to display
 *
 * @example
 * const displayDate = dateToString(new Date)
 * //=> (for en-US locale) Jul 8, 2019
 * @example
 * const displayDate = dateToString(new Date, { year: undefined })
 * //=> (for en-US locale) Jul 8
 */
export function dateToString(date: Date, formatOverrides: object = {}): string {
  return date.toLocaleDateString('default', merge(baseDateFormat, formatOverrides));
}

/**
 * Omni standard for displaying localized time
 *
 * @param      {Date} date                        a Date object
 * @param      {Object} [formatOverides]          any overrides to base display format
 * @returns    {String}                           the time string to display
 *
 * @example
 * const displayTime = dateToTimeString(new Date)
 * //=> (for en-US locale) 9:59 AM
 */
export function dateToTimeString(date: Date, formatOverrides: object = {}): string {
  return date.toLocaleTimeString('default', merge(baseTimeFormat, formatOverrides));
}

/**
 * Omni standard for displaying relative time values
 *
 * @param     {Date} d1                           a Date object
 * @param     {Date} d2                           a second Date object (defaults to now)
 * @param     {String} [locale]                   a locale whose formatting conventions should be used
 * @param     {Object} [optionOverrides]          any overrides to base compare options
 * @returns   {String}                            the relative time string to display
 *
 * @example
 * const displayTime = dateToRelativeTimeString(new Date)
 * //=> (for en-US locale) 3 days ago
 */
export function dateToRelativeTimeString(d1: Date, d2 = new Date(), locale = 'en', optionOverrides) {
  let isRelativeTimeSupported = false;
  try {
    // eslint-disable-next-line no-new
    new Intl.RelativeTimeFormat('en', { numeric: 'auto' });
    isRelativeTimeSupported = true;
  } catch (err) {
    // eslint-disable-next-line no-console
    console.log('Falling back to a regular date/time string due to Intl.RelativeTimeFormat not being supported.');
  }
  let result;
  if (isRelativeTimeSupported) {
    const rtf = new Intl.RelativeTimeFormat(locale, merge(baseRelTimeFormatOptions, optionOverrides));
    //@ts-ignore
    const elapsed = d1 - d2;
    // Derived from https://stackoverflow.com/a/53800501
    Object.entries(timeUnits).some(unitEntry => {
      // Math.abs accounts for both past & future scenarios
      if (Math.abs(elapsed) > unitEntry[1] || unitEntry[0] === 'second') {
        //@ts-ignore
        result = capitalizeFirstWord(rtf.format(Math.round(elapsed / unitEntry[1]), unitEntry[0]));
        return true;
      }
      return false;
    });
  } else {
    //@ts-ignore
    result = dateToString(new Date(d1));
  }
  return result;
}

const baseCompareOptions = { sensitivity: 'base' };

/** sort strings */
export function sortString(a, b, locale, options = baseCompareOptions) {
  const d = a.localeCompare(b, locale, options);
  return d < 0 ? -1 : d > 0 ? 1 : 0;
}

/** sort numbers */
export function sortNumber(a, b) {
  return a < b ? -1 : a > b ? 1 : 0;
}

/**
 * Omni standard for a sorting human-readable strings
 *
 * @param     {Array} arry               an Array of Strings to sort
 * @param     {String} [locale]          a locale whose formatting conventions should be used
 * @param     {Object} [options]         options merged with defaults
 * @param     {Function} [fn]            optional sort operator
 * @returns   {Array}                    a sorted copy of the input Array
 *
 * @example
 * const sortedArry = sort(['2', 'Foo', 'é', 'E', 'e', 'bar', '1'])
 * //=> [ '1', '2', 'bar', 'é', 'E', 'e', 'Foo' ]
 */

export function sort<T>(
  arry: T[],
  locale: string = 'en',
  options: object = {},
  fn: (a, b) => number = (a, b) => sortString(a, b, locale, options as any)
): T[] {
  const merged = { ...baseCompareOptions, ...options };
  // note target is original object to preserve fn reference
  Object.assign(options, merged);
  return [...arry].sort(fn);
}

/**
 * Omni standard for sorting object arrays by a property with human-readable string values
 *
 * @param     {Array} arry               an Array of Objects to sort
 * @param     {String} key               a name of an Object property to access
 * @param     {String} [locale]          a locale whose formatting conventions should be used
 * @param     {Object} [options]         options merged with defaults
 * @param     {Function} [fn]            optional sort operator
 * @returns   {Array}                    a sorted copy of the input Array
 *
 * @example
 * const sortedArry = sortObjects([{name: 'Olga', age: 36}, {name: 'Nathan', age: 40}], 'name')
 * //=> [ {name: 'Nathan', age: 40}, {name: 'Olga', age: 36} ]
 */
export function sortObjects<T>(
  arry: T[],
  key: string,
  locale: string = 'en',
  options: object = {},
  //@ts-ignore
  fn: (a, b) => number = (a, b) => sortString(a[key], b[key], locale, options)
): T[] {
  return sort(arry, locale, options, fn);
}

// Map to store custom responses
const responseForCode = {
  204: () => null
};

function responseOK(res) {
  if (res.ok) {
    if (res.status in responseForCode) {
      res.json = responseForCode[res.status];
    }
    return res;
  }
  return Promise.reject(res);
}

/** fetch a resource
 * see MDN and the various linked content about this API:
 * https://developer.mozilla.org/docs/Web/API/Fetch_API
 * see also AbortController, caching, HTTP headers, etc that relate to various requirements
 */
export function fetching(resource, options) {
  return fetch(resource, options).then(responseOK);
}

const baseFetchOptions = {
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json'
  },
  cache: 'no-store'
};

/** fetch JSON see fetching */
export function fetchJSON(url, options) {
  return fetching(url, merge(baseFetchOptions, options)).then(res => res.json());
}

/** fetch text see fetching */
export function fetchText(url, options) {
  return fetching(url, merge(baseFetchOptions, { headers: { Accept: 'text/plain' } }, options)).then(res => res.text());
}
/** fetch CSS see fetching */
export function fetchCSS(url, options) {
  return fetching(url, merge(baseFetchOptions, { headers: { Accept: 'text/css' } }, options)).then(res => res.text());
}

/**
 * @internal
 * Regexp patern for matching the URL of CSS import at-rules in a given string
 * NOTE:
 * 	 Currently supports the `<at>import "my.css"` variant only
 * 	 Pattern is also used in rollup.config.js, tread lightly
 * @type {RegExp}
 */
export const cssImportAtRulePtn = /\s*@import\s+(?:"|')([^"')]+)(?:"|')\s*/;

/**
 * @internal
 * A workaround for deprecated CSS import at-rules in CSSStyleSheet.replace()
 * NOTE: Only replaces first top-level import found
 * (https://chromestatus.com/feature/4735925877735424)
 *
 * @param     {CSSStyleSheet} sheet      instance of sheet
 * @param     {String} importOrCSS       CSS import statement or CSS string
 * @returns   {Promise}                  promise that resolves to Response object
 *
 * @example (replace <at> with @)
 * const omniStandardStyleSheet = new CSSStyleSheet();
 * styleSheetReplaceImport(omniStandardStyleSheet, '<at>import "/standard.css"')
 * 	 .catch(handleStyleSheetImportError);
 */
export function styleSheetReplaceImport(sheet, importOrCSS) {
  const matches = cssImportAtRulePtn.exec(importOrCSS);
  if (!matches) return sheet.replace(importOrCSS); // is CSS
  //@ts-ignore
  return fetchCSS(matches[1]).then(css => sheet.replace(css)); // is import
}

export function capitalizeFirstWord(phrase) {
  return phrase.replace(/^\w/, c => c.toUpperCase());
}

// trailing slash path insensitive equality
const trailingSlashesPathReplacePtn = /\/+$/;
/** @internal */
export function areUrlsEqualish(url1, url2) {
  const u1 = new URL(url1);
  const u2 = new URL(url2);
  const pathsimilar =
    u1.pathname.replace(trailingSlashesPathReplacePtn, '') === u2.pathname.replace(trailingSlashesPathReplacePtn, '');
  // searchParams may be ordered differently; hash ignored (and should be)
  return u1.origin === u2.origin && pathsimilar && u1.search === u2.search;
}

// derived from https://www.30secondsofcode.org/blog/s/javascript-memoization
export function memoize(fn) {
  return new Proxy(fn, {
    //@ts-ignore
    cache: new Map(),
    apply(target, thisArg, argsList) {
      const cacheKey = JSON.stringify(argsList);
      //@ts-ignore
      if (!this.cache.has(cacheKey)) this.cache.set(cacheKey, target.apply(thisArg, argsList));
      //@ts-ignore
      return this.cache.get(cacheKey);
    }
  });
}

/**
 * Debounce given function so it does not run until wait milliseconds after the last call.
 * If immediate flag is true, then it will run on the first call and not run again until wait milliseconds
 * after the last call.
 *
 * Based off following sources:
 * https://davidwalsh.name/javascript-debounce-function
 * https://levelup.gitconnected.com/debounce-in-javascript-improve-your-applications-performance-5b01855e086
 *
 * @param func {function} function to debounce
 * @param wait {number} time to wait in milliseconds
 * @param immediate {boolean} True if function should be called on the leading edge instead of trailing edge
 * @return {function}
 */
export function debounce(func, wait, immediate = false) {
  let timeout;
  return (...args) => {
    const callNow = immediate && !timeout;
    const later = () => {
      timeout = null;
      if (!immediate) func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func(...args);
  };
}

/**
 * give a file name with an extension and truncate to a smaller version with ellipses in place
 * if filename is < , it returns unmodified
 * if no extension is found in the string, it return unmodified
 * @param {string} filename
 * @param {number=} truncateAt
 * @return {string}
 */
export function truncateFilename(filename: string, truncateAt: number | undefined = 10): string {
  if (!filename.includes('.')) return filename;
  const split = filename.split('.');
  if (split[0].length - 3 <= truncateAt) return filename;
  const first = filename.slice(0, truncateAt);
  const end = split[0].slice(split[0].length - 3, split[0].length);
  split.shift();
  const result = `${first}...${end}.${split.join('.')}`;
  if (result.length > filename.length) return filename;
  return result;
}

/**
 * use browser crypto to get a hash of a string
 */
export async function hashString(string: string, algorithm = 'SHA-256') {
  const bufferIn = new TextEncoder().encode(string);
  const bufferOut = await crypto.subtle.digest(algorithm, bufferIn);
  return [...new Uint8Array(bufferOut)].map(bytes => bytes.toString(16).padStart(2, '0')).join('');
}

/**
 *  - returns first matching element from depth-first dom traversal
 *  - comparable to `document.querySelector`, but this adds the ability to pierce shadow roots indefinitely
 *  - useful when you would want to get a reference to an element that is outside of your current root
 *  - not as useful to query for an internal element that you don't control, .e.g., omni-table internal elements
 *  @example
 *  ```js
 *     // if dom currently has `<omni-card id="1234"></omni-card>` within any root, the element is returned
 *     const element = querySelectorDeep('omni-card', '1234');
 *     // then you can call an element method
 *     element.doSomething();
 *     // to narrow the scope of your query, provide a shadow root
 *     // the element will be returned if the `<omni-card id="1234"></omni-card>` is within or a descendant of the given shadowRoot
 *     // ... in your element class
 *     const element = querySelectorDeep('omni-card', '1234', this.shadowRoot);
 *  ```
 *  @param {string} tagName the tag name of the element
 *  @param {string=} id (optional) the dom id for the element
 *  @param {Document | ShadowRoot=} rootNode (optional) pass a shadow root to only include that shadow root and its descendants
 *  @returns {HTMLElement | undefined} element | undefined  
 */
export function querySelectorDeep(
  tagName: string,
  id: string = null,
  rootNode: Document | ShadowRoot = document
): HTMLElement | undefined {
  function traverseNodes(nodeList) {
    for (const node of nodeList) {
      if (node.localName === tagName && (id ? id === node.id : true)) {
        return node;
      }
      if (node.shadowRoot) {
        const found = traverseNodes(node.shadowRoot.querySelectorAll('*'));
        if (found) {
          return found;
        }
      }
    }
  }
  return traverseNodes(rootNode.querySelectorAll('*'));
}

/** @internal */
export interface ResizeOptions {
  edits: {
    resize: {
      width: number;
      height: number;
      fit: 'cover' | 'contain' | 'fill' | 'inside' | 'outside';
    };
  };
  toFormat: 'png' | 'jpeg' | 'tiff' | 'webp' | 'raw';
}

/**
 * @param {string} url -> Image URL with image key(encode in url)
 * @param {ResizeOptions} options  -> the resize options
 * @returns {string} -> New image URL with all modification as specified in the options object.
 */
export function resizeImage(url: string, options: ResizeOptions): string {
  const arr = url.split('/');
  const encodedStr = arr[arr.length - 1].split('#')[0];
  const decodedObj = JSON.parse(atob(encodedStr));
  const newOption = {
    key: decodedObj.key,
    edits: options.edits
  };
  const encodeObj = btoa(JSON.stringify(newOption));
  arr.splice(arr.length - 1, 1);
  return arr.join('/') + '/' + encodeObj;
}

/**
 * - get element size including margin (default includes margin);
 * - numbers are integers
 * - will include padding if element is border-box (default for omni-elements)
 *  @param {HTMLElement} element the tag name of the element
 *  @param {boolean=} includeMargin (optional) the dom id for the element
 *  @returns {{width: number, height: number}} width and height
 */
export function getElementSize(element: HTMLElement, includeMargin: boolean = true): { width: number; height: number } {
  let { width, height } = element.getBoundingClientRect();
  if (includeMargin) {
    const computedStyle = getComputedStyle(element);
    width += parseInt(computedStyle.marginLeft) + parseInt(computedStyle.marginRight);
    height += parseInt(computedStyle.marginTop) + parseInt(computedStyle.marginBottom);
  }
  return {
    width: Math.trunc(width),
    height: Math.trunc(height)
  };
}

/** @internal */
export type Environment = 'local' | 'dev' | 'qa' | 'stg' | 'prod';

/**
 * @internal
 * Treat local environment as dev, all others are themselves
 */
export const ssoEnv = (env: Environment): Environment => (env === 'local' ? 'dev' : env);

/**
 * @internal
 * Determine the hostname given a base hostname and env.
 * "local" + "omni.annalect.com" => "devomni.annalect.com"
 */
export const ssoHost = (env: Environment, host: string) => {
  let prefix: Environment | '' = ssoEnv(env);
  if (prefix === 'prod') prefix = '';
  return `${prefix}${host}`;
};

/** @internal */
export const fetchOmni = async (env: Environment, path: string, options) => {
  // fetchJSON fails as it adds headers rejected by CORS; using fetch directly
  const url = `https://${ssoHost(env, 'omni.annalect.com')}${path}`;
  const response = await fetch(url, options);
  return response.json();
};

export const generateToken = async (
  env: Environment,
  clientId: string,
  sid: string,
  typeKey: string
): Promise<string> =>
  (
    await fetchOmni(env, `/api/jwt/${clientId}/?ANsid=${sid}`, {
      method: 'POST',
      body: JSON.stringify({
        omniTypeKeys: [typeKey],
        withRoles: true
      })
    })
  ).jwt;

interface JWTPayload {
  client_guid: string;
  omniTypeKeys?: string[];
  ANsid: string;
  roles?: string[];
}

export const parseJWT = (token: string): JWTPayload => {
  const base64Url = token.split('.')[1];
  const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
  const jsonPayload = decodeURIComponent(
    window
      .atob(base64)
      .split('')
      .map(c => `%${`00${c.charCodeAt(0).toString(16)}`.slice(-2)}`)
      .join('')
  );
  return JSON.parse(jsonPayload);
};

export const userHasRole = (jwt: string, typeKey: string, requiredRoles: any[] = []): boolean => {
  try {
    const { omniTypeKeys, roles } = parseJWT(jwt);
    const appIndex = omniTypeKeys?.indexOf(typeKey);
    // Get user roles for this app
    const userRoles = roles?.[appIndex]?.split(',') ?? [];
    // Intersect with list of roles required for this app
    const matches = userRoles.filter(x => requiredRoles.includes(x));
    return matches.length > 0;
  } catch {
    return false;
  }
};
