import { html, nothing } from 'lit';
import { property, state } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { ifDefined } from 'lit/directives/if-defined.js';
import { repeat } from 'lit/directives/repeat.js';
import { OmniElement } from '../../omni-element.js';

import {css} from 'lit';
const styles = css`:host{--cell-bg-editable: var(--color-pale-grey-two);--text-color-editable: #3b3e3f;--table-row-background-color: var(--color-white);--table-row-highlight-color: rgba(254, 216, 130, 0.35);--cell-expand-collapse-max-width: 24ch}.table-container{width:100%;height:100%}th.is-sortable{cursor:pointer}th>span{transition:color .25s ease}th.is-sortable:hover>span,th.is-sorted>span{color:var(--color-electric-blue)}th{position:relative}th>omni-icon{transition:fill .25s ease;position:absolute;margin:3px}th.is-sortable:hover>omni-icon,th.is-sorted>omni-icon{fill:var(--color-electric-blue)}th>*,td>*{vertical-align:middle}tr:hover .is-editable,tr:hover .input.is-static.is-editable{color:var(--text-color-editable);background-color:var(--cell-bg-editable)}tr.header,tr.header:hover{background-color:rgba(0,0,0,0) !important}tr,tr:hover{background-color:var(--table-row-background-color) !important}tr.highlight{background-color:var(--table-row-highlight-color) !important}tr.is-hovered{transform:none !important}td.is-main{font-weight:600;color:var(--color-almost-black);transition:color .25s ease}td.is-main:hover{color:var(--color-electric-blue);cursor:pointer}.omni th.is-clamped,.omni td.is-clamped{max-width:24ch;display:table-cell}.omni th.is-clamped>*,.omni td.is-clamped>*{display:inline-block;max-width:100%;overflow:hidden;text-overflow:ellipsis}td{background-color:rgba(0,0,0,0) !important}td:hover{background-color:rgba(0,0,0,0) !important}td.expand,td.expand:hover{width:60px;background-color:rgba(0,0,0,0) !important;padding:0 !important;position:relative}.expand .expand-wrapper{padding:.6rem;height:100%;display:flex;align-items:center;justify-content:flex-start}.expand-wrapper button.is-text.is-stateless{border:none}.expand-wrapper.is-clamped{max-width:calc(var(--cell-expand-collapse-max-width) + 56px)}.expand-wrapper.is-clamped span.expand-content{display:inline-block;max-width:var(--cell-expand-collapse-max-width);overflow:hidden;text-overflow:ellipsis}.expand-spacer{width:41px;min-width:41px}.leftbar{position:absolute;left:0;top:50%;transform:translateY(-50%);height:30px;width:3px;border-top-right-radius:5px;border-bottom-right-radius:5px}.line-container{margin:-1rem 0;padding:1rem 0}.line-container .line{background-color:rgba(0,0,0,0);height:2px;margin:-1px 0}.line-container[drag-over]{position:relative}.line-container[drag-over] .circle-1{background-color:var(--color-primary);position:absolute;top:50%;left:0;transform:translate(-50%, -50%);height:5px;width:5px;border-radius:50%}.line-container[drag-over] .line{background-color:var(--color-primary);height:2px}.line-container[drag-over] .circle-2{background-color:var(--color-primary);position:absolute;top:50%;right:-3px;transform:translate(-50%, -50%);height:5px;width:5px;border-radius:50%}`


import { sortObjects } from '../../omni-util.js';
import type { KeyFn, NestedDataSortDetail, Sort, TableColumn, TableData, TableRow } from './omni-table.types.js';

type TableSet = Set<string | number | TableRow>;
interface ExpandCellConfig {
  row: TableRow;
  level: number;
  template: unknown;
  shouldClamp: boolean;
  isMain: boolean;
}
/**
 * To include within your project `import { OmniTable } from 'omni-ui';`
 *
 * @tagname omni-table
 *
 * @event {CustomEvent<Sort | {data: TableData, sort: Sort}>} sort - fires when a column header is clicked
 * @event {TableRow} hover-row-enter - fires when a row is hovered
 * @event {TableRow} hover-row-exit - fires when a row is no longer hovered
 * @event {TableRow} expand-or-collapse-rows - fires when a row is expanded or collapsed
 * @event {{moveFromIdx: number, moveToIdx: number}} omni-drop - fires when a row is reordered
 */
class OmniTable extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  #firstUpdateCompleted = false;
  @state() private _treeSet: TableSet = new Set();
  @state() private _highlightSet: TableSet = new Set();
  @state() private _hoverSet: TableSet = new Set();
  @state() private _nestedDataExists: boolean = false;
  /** number of pixels to indent each nested row */
  @property({ type: Number }) nestedIndent: number = 10;
  /** override the defaultKeyFn to specify uniqueness for a row in the nested table. Similar to the Lit keyFn */
  @property({ attribute: false }) keyFn: KeyFn;
  /** nested table expands all on load */
  @property({ type: Boolean }) expandAllOnLoad: boolean = false;
  /** hide nested indent for nested rows */
  @property({ type: Boolean }) hideNestedIndent: boolean = false;
  /** an array of column config objects */
  @property({ type: Array, attribute: false }) columns: TableColumn[] = [];
  /** an array of row data objects */
  @property({ type: Array, attribute: false }) data: TableData = [];
  /** object to track current sort state */
  @property({ type: Object, attribute: false }) sort: Sort | Record<string, unknown> = {};
  /** enable dragging rows to reorder. This is intentionally spelled incorrectly to differentiate from the native 'draggable' attribute */
  @property({ type: Boolean, attribute: 'drag-able' }) dragAble: boolean = false;

  constructor() {
    super();
    this.keyFn = this.#defaultKeyFn;
  }

  async firstUpdated() {
    await this.updateComplete;
    if (this.dragAble) {
      const { DragAndDropRowsController } = await import('./omni-drag-and-drop-rows.js');
      new DragAndDropRowsController(
        this,
        this.shadowRoot.querySelector('table'),
        this.shadowRoot.querySelector('tbody'),
        this._nestedDataExists
      );
    }
  }

  #defaultKeyFn = (row: TableRow): string | number | TableRow => {
    const isMainKey = this.columns.find(one => one.isMain)?.key;
    const firstColumnKey = this.columns.find(one => one.key)?.key;
    return (row[isMainKey] as string | number) ?? (row[firstColumnKey] as string | number) ?? row;
  };

  async willUpdate(changed) {
    if (changed.has('data')) {
      this._nestedDataExists = this.data.some(val => val.nestedData);
      if (this._nestedDataExists && this.expandAllOnLoad && !this.#firstUpdateCompleted) {
        this.expandAll();
      }
      this.#firstUpdateCompleted = true;
    }
  }

  private get _headersTemplate() {
    const columns = this.columns.map(column => {
      const { key, dir } = this.sort;
      const isUp = (dir === 'asc' && key === column.key) || (dir === 'desc' && key !== column.key);
      const sortIconId = `omni:interactive:${isUp ? 'up' : 'down'}`;
      return html`
        <th
          align
          part="th table-header-cell table-header-${column.key}"
          @click="${this._headerCellClicked}"
          class="${classMap({
            'is-clamped': true,
            'is-uppercase': true,
            'is-size-6': true,
            'is-sortable': column.isSortable ? true : false,
            'is-sorted': column.key ? key === column.key : false
          })}"
          data-key="${ifDefined(column.key)}">
          <span part="th-content">${column.label}</span>
          ${column.isSortable ? html`<omni-icon class="is-size-4" icon-id="${sortIconId}"></omni-icon>` : nothing}
        </th>
      `;
    });
    return columns;
  }

  private get _rowsTemplate() {
    return repeat(this.data, this.keyFn, (row, idx) => this._buildRow(row, idx));
  }

  private _draggableCell(level, id) {
    if (this.dragAble) {
      if (level === 0) {
        return html`
          <td style="padding: 0; padding-left: 25px;" part="draggable-cell-${id}">
            <omni-icon icon-id="omni:interactive:reorder" style="cursor: grab"></omni-icon>
          </td>
        `;
      } else {
        return html`<td></td>`;
      }
    } else {
      return nothing;
    }
  }

  private _buildRow(row: TableRow, idx: number, level = 0) {
    // string or number result from keyFn will keep id with row through sort. If object, level and index will be placed by current level and index
    const id =
      typeof this.keyFn(row) === 'string' || typeof this.keyFn(row) === 'number'
        ? String(this.keyFn(row))
        : `L${level}-I${idx}`;
    return html`
      ${this.dragAble && level === 0
        ? html` <tr>
            <td colspan="${this.columns.length + 1}" style="padding: 0; height: auto; border: none">
              <div class="line-container" line-id="${idx}">
                <span class="circle-1"></span>
                <div class="line"></div>
                <span class="circle-2"></span>
              </div>
            </td>
          </tr>`
        : nothing}
      <tr
        part="table-body-row"
        class="${row.highlight || this._highlightSet.has(this.keyFn(row)) ? 'highlight' : ''} ${this._hoverSet.has(
          this.keyFn(row)
        )
          ? 'is-hovered'
          : ''}"
        id=${id}
        drag-id=${idx}
        @mouseover=${() => this.dispatchNewEvent('hover-row-enter', { detail: row })}
        @mouseout=${() => this.dispatchNewEvent('hover-row-exit', { detail: row })}>
        ${this._draggableCell(level, id)}
        ${this.columns.map((column, cIdx) => this._cellTemplate(row, idx, column, cIdx, level))}
      </tr>
      ${this.dragAble && level === 0 && idx === this.data.length - 1
        ? html`
            <tr>
              <td colspan="${this.columns.length + 1}" style="padding: 0; height: auto; border: none">
                <div class="line-container" line-id="${idx + 1}">
                  <span class="circle-1"></span>
                  <div class="line"></div>
                  <span class="circle-2"></span>
                </div>
              </td>
            </tr>
          `
        : nothing}
      ${row.nestedData && this._treeSet.has(this.keyFn(row))
        ? repeat(row.nestedData as TableRow[], this.keyFn, (nRow, nIdx) => this._buildRow(nRow, nIdx, level + 1))
        : nothing}
    `;
  }

  private _cellTemplate(row: TableRow, idx: number, column: TableColumn, cIdx: number, level: number) {
    let value = column.passthrough ? row : row[column.key];
    if (value === null || value === undefined) value = column.default;
    value = column.format ? column.format(value, idx) : value;
    if (this._nestedDataExists && cIdx === 0 && !this.hideNestedIndent) {
      const expandCellConfig: ExpandCellConfig = {
        row,
        level,
        template: column.template
          ? column.template(value, idx)
          : html`<span part="td-content" class="expand-content">${value}</span>`,
        shouldClamp: column.isClamped ?? true,
        isMain: column.isMain
      };
      return this.#expandCollapseCellTemplate(expandCellConfig);
    }
    if (column.template) {
      return column.template(value, idx);
    }

    return html`
      <td
        part="td table-body-cell table-body-${column.key}"
        class="${classMap({
          'is-clamped': column.isClamped ?? true,
          'is-main': column.isMain
        })}">
        <span part="td-content">${value}</span>
      </td>
    `;
  }

  #expandCollapseCellTemplate({ row, level, template, shouldClamp, isMain }: ExpandCellConfig) {
    const leftbar = color => html` <span class="leftbar" style="background-color: ${color}"></span> `;
    return html`
      <td
        class="expand ${isMain ? 'is-main' : ''}"
        part="td-expand-collapse-level-${level}-${row.nestedData ? 'has-children' : 'no-children'}">
        ${row.leftbar ? leftbar(row.leftbar) : nothing}
        <div
          class="expand-wrapper ${shouldClamp ? 'is-clamped' : ''}"
          style="margin-left: ${level * this.nestedIndent + 'px'}">
          ${row.nestedData
            ? html`
                <button
                  class="button is-text is-stateless"
                  alt="Indicator to expand or collapse nested rows"
                  @click=${() => this.#toggleShowHideChildren(row)}>
                  <omni-icon
                    class="is-size-1"
                    icon-id="omni:interactive:${this._treeSet.has(this.keyFn(row)) ? 'down' : 'right'}"></omni-icon>
                </button>
              `
            : html`<div class="expand-spacer"></div>`}
          ${template}
        </div>
      </td>
    `;
  }

  private _findColumn(col: TableColumn, key: string) {
    return col.isSortable && col.key === key;
  }

  private _headerCellClicked(e: Event & { currentTarget: HTMLElement }) {
    const { key = '' } = e.currentTarget.dataset;
    if (!key) {
      return;
    }
    const column = this.columns.find(col => this._findColumn(col, key));
    if (!column) {
      return;
    }

    // Update sort column and direction
    if (key === this.sort.key) {
      // Same column, update sort direction
      this.sort = { ...this.sort, dir: this.sort.dir === 'asc' ? 'desc' : 'asc' };
    } else {
      // Different column, update sort key for that column
      this.sort = { ...this.sort, key, dir: 'asc' };
    }

    // Emit event with new sort column and direction
    let evtDetail = { detail: { ...this.sort } };
    if (this._nestedDataExists) {
      evtDetail = { detail: { sort: this.sort, data: this.data } };
    }
    this.dispatchNewEvent('sort', evtDetail);
  }

  #toggleShowHideChildren(row: TableRow) {
    this._treeSet.has(this.keyFn(row)) ? this._treeSet.delete(this.keyFn(row)) : this._treeSet.add(this.keyFn(row));
    this._treeSet = new Set(this._treeSet);
    this.dispatchNewEvent('expand-or-collapse-rows', { detail: row });
  }

  /** returns a flat array of the passed table data */
  #flatten(data: TableData): TableRow[] {
    const flattened = [];
    const walk = levelData => {
      for (const row of levelData) {
        flattened.push(row);
        if (row.nestedData) {
          walk(row.nestedData);
        }
      }
    };
    walk(data);
    return flattened;
  }

  /** toggle the highlight for a given row	*/
  public toggleRowHighlight(row: TableRow) {
    this._highlightSet.has(this.keyFn(row))
      ? this._highlightSet.delete(this.keyFn(row))
      : this._highlightSet.add(this.keyFn(row));
    this._highlightSet = new Set(this._highlightSet);
  }

  public toggleRowExpansion(row: TableRow) {
    this._treeSet.has(this.keyFn(row)) ? this._treeSet.delete(this.keyFn(row)) : this._treeSet.add(this.keyFn(row));
    this._treeSet = new Set(this._treeSet);
  }

  /** if children exist, expands rows for provided array of rows	*/
  public expandRows(rows: TableRow[]) {
    rows.forEach(row => this._treeSet.add(this.keyFn(row)));
    this._treeSet = new Set(this._treeSet);
    this.dispatchNewEvent('expand-or-collapse-rows');
  }

  /**if children exist, collapse rows for provided array of rows	*/
  public collapseRows(rows: TableRow[]) {
    rows.forEach(row => this._treeSet.delete(this.keyFn(row)));
    this._treeSet = new Set(this._treeSet);
    this.dispatchNewEvent('expand-or-collapse-rows');
  }

  /** collapse all rows with nested data */
  public collapseAll() {
    this.collapseRows(this.#flatten(this.data));
  }

  /** expand all rows with nested data */
  public expandAll() {
    this.expandRows(this.#flatten(this.data));
  }

  public toggleRowHover(row: TableRow) {
    this._hoverSet.has(this.keyFn(row)) ? this._hoverSet.delete(this.keyFn(row)) : this._hoverSet.add(this.keyFn(row));
    this._hoverSet = new Set(this._hoverSet);
  }

  public hoverRow(row: TableRow) {
    this._hoverSet.add(this.keyFn(row));
    this._hoverSet = new Set(this._hoverSet);
  }

  public removeHoverRow(row: TableRow) {
    this._hoverSet.delete(this.keyFn(row));
    this._hoverSet = new Set(this._hoverSet);
  }

  render() {
    return html`
      <omni-style>
        <div class="table-container" part="table-container">
          <table part="table" class="table is-fullwidth is-hoverable">
            <thead part="table-header">
              <tr part="table-header-row" class="header">
                ${this.dragAble ? html`<th style="padding: 0; width: 0"></th>` : nothing} ${this._headersTemplate}
              </tr>
            </thead>
            <tbody part="table-body">
              ${this._rowsTemplate}
            </tbody>
          </table>
          <div id="table-footer">
            <slot name="table-footer"></slot>
          </div>
        </div>
      </omni-style>
    `;
  }
}

OmniElement.register('omni-table', OmniTable);

declare global {
  interface HTMLElementTagNameMap {
    'omni-table': OmniTable;
  }
}

function handleNestedDataSort({ detail: { data, sort } }: CustomEvent<NestedDataSortDetail>): NestedDataSortDetail {
  const sortLevel = (levelData: TableRow[]) => {
    const sortedData = sortObjects(levelData, sort.key);
    if (sort.dir === 'desc') {
      sortedData.reverse();
    }
    for (const obj of sortedData) {
      if (obj.nestedData) {
        obj.nestedData = sortLevel(obj.nestedData as TableRow[]);
      }
    }
    return sortedData;
  };
  const sortedData = sortLevel(data);
  return { data: sortedData, sort: sort };
}

export {
  OmniTable,
  handleNestedDataSort,
  type KeyFn,
  type NestedDataSortDetail,
  type Sort,
  type TableColumn,
  type TableData,
  type TableRow
};
