/**
 * To include within your project `import { OmniDropdown } from 'omni-ui';`
 *
 * @tagname omni-dropdown
 * @attr {boolean} required - adds required attribute to dropdown for form validation
 * @event {Option | Option[]} change - fires when selection changes
 */
export class OmniDropdown extends ElementInternalsBase<Option[]> {
  static get styles() {
    return [...super.styles, styles];
  }

  /** given an option, find first group header with higher index than option */
  public static findGroupHeaderForItem(option: Option, allOptions: Option[]): GroupHeader | null {
    const index = allOptions.indexOf(option);
    const reversedOptions = [...allOptions].reverse();
    for (const option of reversedOptions.slice(reversedOptions.length - index)) {
      if (typeof option === 'object' && (option as GroupHeader).groupheader) {
        return option as GroupHeader;
      }
    }
    return null;
  }

  #clickOutsideController = new ClickOutsideController(this, () => {
    this.opened = false;
    this._searchTerm = '';
  });

  @state() private _selected = new Set<Option>();
  @state() private _disabled = new Set<Option>();
  @state() private _disabledOnMaxSet = new Set<Option>();
  @state() private _computedTargetWidth: number;
  @state() private _computedLabelWidth: number;
  @state() private _searchTerm: string = '';
  @state() private _filteredOptions: Option[] = [];
  @state() private _isLoading: boolean = false;
  @state() private _hasGroupHeaders: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) opened: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) disableonmax: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) multiselect: boolean = false;
  @property({ type: Boolean, attribute: true }) preventLastRemoval: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) noselect: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) selectall: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) typeahead: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) searchindropdown: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) dynamicadditions: boolean = false;
  @property({ type: Boolean, attribute: true, reflect: true }) hidefooter: boolean = false;
  @property({ type: String }) name: string;
  @property({ type: String }) valueMissingMessage: string = 'Please select an option';
  @property({ type: String }) label = '';
  @property({ type: String }) labelTooltip: string | TemplateResult = '';
  @property({ type: String }) placeholder = 'Select an option';
  @property({ type: String }) searchPlaceholder = 'Search';
  @property({ type: String }) helpText = '';
  /** error message will show when set */
  @property({ type: String }) error = '';
  @property({ type: String }) disabledMessage: string = '';
  @property({ type: String }) labelPosition: 'top' | 'left' = 'top';
  @property({ type: String }) dropdownAlignment: 'extend-right' | 'vertical' = 'vertical';
  /** min: an integer: 1 or greater */
  @property({ type: Number }) min: number;
  /** max: an integer: 1 or greater */
  @property({ type: Number }) max: number;
  @property({ type: Number }) inputDebounceTimer: number = 0;
  // label variant specific properties
  @property({ type: String }) variant: 'default' | 'label' = 'default';
  @property({ type: Boolean, attribute: true, reflect: true }) showxicon: boolean = false;

  @property({ attribute: false }) options: Option[] = [];
  /** the current value of the dropdown (pass an array with a single element for single-select and pass an arry with elements for multiselect */
  @property({ attribute: false }) value: Option[] = [];
  @property({ attribute: false }) fetchOptionsFn: FetchOptionsFn;
  /** add an html template to the end of all dropdown options */
  // eslint-disable-next-line @typescript-eslint/no-redundant-type-constituents
  @property({ attribute: false }) optionsPostTemplate: (option: Option) => TemplateResult | unknown;
  /** adjust the character limit of the pill-list */
  @property({ type: Number }) pillListSliceAt: number = 10;

  @query('.target') private targetEl: HTMLDivElement;
  @query('.label-container') private labelEl: HTMLDivElement;

  async connectedCallback() {
    super.connectedCallback();
    this.addEventListener('keyup', this.#handleKeysOnDropdown as EventListener);
    this.tabIndex = 0;
    this._internals.setFormValue('');
  }

  disconnectedCallback(): void {
    super.disconnectedCallback();
    this.removeEventListener('keyup', this.#handleKeysOnDropdown as EventListener);
  }

  async willUpdate(changed: Map<string, unknown>) {
    if (changed.has('options')) {
      this.options.forEach(option => {
        if (option && typeof option === 'object' && option.disabled) {
          this._disabled.add(option);
        }
        if (option && isGroupHeader(option)) {
          this._disabled.add(option);
        }
      });
      this._hasGroupHeaders = this.options?.some(option => isGroupHeader(option));
      this._isLoading = true;
      this._filteredOptions = await this.#getFilteredOptions();
      this._isLoading = false;
    }
    if ((changed.has('min') || changed.has('max')) && !this.multiselect) {
      console.warn(`${this.localName}: setting min or max is only allowed when multiselect = true`);
    }
    // rechecks validity on value, min, max changes
    if (changed.has('value') || changed.has('min') || changed.has('max')) {
      // only update if the value has changed
      // use toString() to compare arrays' values, not their references
      if ((changed.get('value') as Option[] | undefined)?.toString() !== this.value?.toString()) {
        // to allow dynamic "removing" from .value, clear set before adding values
        this._selected.clear();
        const add: Option[] = [];
        for (const option of this.value) {
          if (!this._selected.has(option)) {
            add.push(option);
          }
        }
        this.#commitSelections(add, false, true);
      }
    }
    if (changed.has('variant') && this.variant === 'label') {
      // override some properties only for the label variant
      this.dropdownAlignment = 'extend-right';
      this.helpText = '';
      this.label = '';
      this.labelTooltip = '';
      this.multiselect = false;
      this.error = '';
      this.labelPosition = 'top';
      this.hidefooter = true;
      this.typeahead = false;
      this.searchindropdown = false;
      this.selectall = false;
    }

    if (changed.has('fetchOptionsFn')) {
      if (this.options?.length) {
        console.warn('<omni-dropdown>: options should not be set by property when providing a fetchOptionsFn');
      }
    }
    if (changed.has('_searchTerm')) {
      this._isLoading = true;
      this.#getFilteredOptions().then(options => {
        this._filteredOptions = options;
        this._isLoading = false;
      });
    }
    if (changed.has('_selected') && this.disableonmax) {
      this.#disableOnMaxLimit();
    }
  }

  #disableOnMaxLimit() {
    if (this.max && this._selected.size >= this.max) {
      this.options.forEach(option => {
        if (!this._selected.has(option)) {
          this._disabledOnMaxSet.add(option);
        }
      });
    } else {
      this._disabledOnMaxSet = new Set();
    }
  }

  async firstUpdated() {
    await this.updateComplete;
    if (this.required && !this._selected.size) {
      this._internals.setValidity({ valueMissing: true }, this.valueMissingMessage, this.targetEl);
    }
    // compute initial widths
    this._computedTargetWidth = this.targetEl.clientWidth;
    this._computedLabelWidth = this.labelEl ? getElementSize(this.labelEl).width : null;
    this._isLoading = true;
    this.#getFilteredOptions().then(options => {
      this._isLoading = false;
      this._filteredOptions = options;
    });
  }

  updated(changed: Map<string, unknown>) {
    // recalculate computed widths on each update; skip if is the same prop
    if (!changed.has('_computedTargetWidth')) {
      this._computedTargetWidth = this.targetEl.clientWidth;
    }
    if (!changed.has('_computedLabelWidth')) {
      this._computedLabelWidth = this.labelEl ? getElementSize(this.labelEl).width : null;
    }
  }

  async #getFilteredOptions(): Promise<Option[]> {
    if (this.fetchOptionsFn) {
      return this.fetchOptionsFn(this._searchTerm);
    }

    const groupHeadersExist = this.options.some(
      option => typeof option === 'object' && (option as GroupHeader).groupheader
    );
    const foundInSearch: { match: boolean; option: Option }[] = [];
    for (const option of this.options) {
      if (!this.typeahead) {
        foundInSearch.push({ match: true, option });
      } else if (typeof option === 'object' && (option as GroupHeader).groupheader) {
        foundInSearch.push({ match: false, option });
      } else {
        const val = String(this.#toDisplayValue(option));
        const match = val.toLowerCase().trim().includes(this._searchTerm.trim().toLowerCase());
        foundInSearch.push({ match, option });
      }
    }
    // if the option is a group header, and any of the items in the group match, show the group header
    if (groupHeadersExist && this.typeahead) {
      const groupHeaders = new Set();
      for (const result of foundInSearch) {
        if (!isGroupHeader(result.option) && result.match) {
          const header = OmniDropdown.findGroupHeaderForItem(result.option, this.options);
          if (header) {
            groupHeaders.add(header);
          }
        }
      }
      foundInSearch.forEach(result => {
        if (groupHeaders.has(result.option)) {
          result.match = true;
        }
      });
    }

    return foundInSearch.filter(result => result.match).map(result => result.option);
  }

  #handleKeysOnDropdown = (e: KeyboardEvent) => {
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    this.updateComplete.then(() => {
      if (e.key === 'Enter' && !this.opened) {
        this.#handleTargetClicked(e);
      }
      if (e.key === 'Escape') {
        this.#handleTargetClicked(e, true);
      }
    });
  };

  #toDisplayValue(option: Option) {
    return option && typeof option === 'object' ? option.value : option;
  }

  #handleRemoveItem(e: CustomEvent<Option>): void {
    e.stopPropagation();
    if (!this.disabled) {
      this.#handleSelectChange(e);
    }
  }

  #emitChange(): void {
    const [firstSelected] = [...this._selected.values()];
    const allSelected = [...this._selected.values()];
    const eventDetail = {
      detail: this.multiselect ? allSelected : firstSelected ? firstSelected : null
    };
    this.dispatchNewEvent('change', eventDetail);
  }

  get #selectedToFormDataString() {
    return [...this._selected.values()]
      .map(one => {
        // TODO: decide if should an object be mapped to value, or left as the object?
        if (one && typeof one === 'object') {
          return String(one.value);
        }
        return String(one);
      })
      .join(',');
  }

  get #labelTemplate(): TemplateResult | typeof nothing {
    if (!this.label) return nothing;
    const tooltipLabel = html`
      <div class="label-container ${this.labelPosition}">
        <label part="label">${this.label}</label>
        ${this.labelTooltip
          ? html`
              <omni-tooltip>
                <omni-icon slot="invoker" icon-id="icon:informative:info"></omni-icon>
                <span slot="content">${this.labelTooltip}</span>
              </omni-tooltip>
            `
          : nothing}
      </div>
    `;
    return tooltipLabel;
  }

  /** toggles opened property and emits event */
  #handleTargetClicked(e, opened = this.opened) {
    this.opened = this.disabled ? false : !opened;
    if (this.typeahead && !this.searchindropdown && !this.opened) {
      this.opened = true;
    }
    this.dispatchNewEvent('dropdown', { detail: this.opened });
  }

  #handleSearchChange({ detail: { value } }: CustomEvent<{ value: string }>) {
    this._searchTerm = value;
  }

  get #searchInputTemplate() {
    return html`
      <omni-search
        .inputDebounceTimer=${this.inputDebounceTimer}
        .value=${this._searchTerm}
        .ph="${this.searchPlaceholder}"
        isopen
        @search-update=${this.#handleSearchChange}>
      </omni-search>
    `;
  }

  // TODO decide what the 'clear' event does. does it clear the current selection and emit an event?
  #handleVariantClear(e: Event) {
    e.stopPropagation();
    this.reset();
    this.dispatchNewEvent('clear');
  }

  get #innerTargetTemplateForType() {
    const targetTypes: Record<string, () => TemplateResult | typeof nothing> = {
      placeholder: () => {
        const sliceAt = 30;
        const placeholderToDisplay =
          this.placeholder.slice(0, sliceAt) + (this.placeholder.length > sliceAt ? '...' : '');
        return html`<p class="${this.variant === 'label' ? 'single-select label-variant' : ''}">
          ${placeholderToDisplay}
        </p>`;
      },
      one: () => {
        const selected = [...this._selected.values()][0];
        return html`<p class="single-select ${this.variant === 'label' ? 'label-variant' : ''}">
          ${this.#toDisplayValue(selected)}
        </p>`;
      },
      multi: () => {
        if (!this._computedTargetWidth) return nothing;
        const selectedForPillList: Option[] = [...this._selected.values()];
        const innerWidths = 20 + 18 + 26 + 30; // padding-left, tooltip width, arrow width, padding-right
        return html`<omni-pill-list
          class="${this.disabled ? 'enable-tooltip' : ''}"
          .items=${selectedForPillList}
          .overflowWidth=${this._computedTargetWidth - innerWidths}
          .sliceAt=${this.pillListSliceAt}
          @remove=${this.#handleRemoveItem}>
        </omni-pill-list>`;
      },
      typeahead: () => this.#searchInputTemplate
    };
    if (this.multiselect && this._hasGroupHeaders) {
      return targetTypes.placeholder();
    }
    if (this.typeahead && !this.searchindropdown) {
      return targetTypes.typeahead();
    }
    if (this.placeholder && !this._selected.size) {
      return targetTypes.placeholder();
    }
    if (this._selected.size === 1 && !this.multiselect) {
      return targetTypes.one();
    }
    return targetTypes.multi();
  }

  get #targetTemplate(): TemplateResult {
    const classes = classMap({
      target: true,
      opened: this.opened,
      filled: this._selected.size > 0,
      'has-error': this.error ? true : false, // TODO: make this work with internals
      typeahead: this.typeahead && !this.searchindropdown,
      [this.labelPosition]: true,
      'label-variant': this.variant === 'label'
    });
    return html`
      <div class="${classes}" part="target ${this.opened ? 'opened' : ''}" @click=${this.#handleTargetClicked}>
        ${this.#innerTargetTemplateForType}
        <div class="target-icons">
          ${!this.typeahead || this.searchindropdown
            ? html`<omni-icon icon-id="omni:interactive:${this.opened ? 'up' : 'down'}"> </omni-icon>`
            : nothing}
          ${this.variant === 'label' && this._selected.size && this.showxicon
            ? html`<omni-icon
                class="x-close is-size-6"
                icon-id="icon:interactive:remove"
                @click=${this.#handleVariantClear}></omni-icon>`
            : nothing}
        </div>
      </div>
    `;
  }

  #handleEnterOnItem(e: CustomEvent<{ eventKey: string; value: Option }>) {
    if (e.detail.eventKey === 'Enter') {
      this.#commitSelections([e.detail.value]);
    }
  }

  #handleDynamicAddition() {
    this.options.push(this._searchTerm);
    this.select([this._searchTerm]);
    this.#handleTargetClicked(null);
    this._searchTerm = '';
  }

  #handleSelectChange({ detail: activeOption }: CustomEvent<Option>) {
    this.#commitSelections([activeOption]);
  }

  get #dropdownTemplate(): TemplateResult | typeof nothing {
    if (!this.opened) return nothing;
    const classes = classMap({
      dropdown: true,
      'no-label': !this.label,
      top: this.labelPosition === 'top',
      left: this.labelPosition === 'left',
      [this.dropdownAlignment]: true,
      'label-variant': this.variant === 'label',
      'cursor-default': this._isLoading || !this._filteredOptions?.length,
      padding: this._filteredOptions?.length || this._searchTerm || this._isLoading || this.searchindropdown
    });
    // left label position is secondary, so rather than use classes, use calculated positions to align
    const inlineStyles = styleMap(
      this.labelPosition === 'left'
        ? {
            ...(this._computedLabelWidth ? { left: `${this._computedLabelWidth}px` } : {}),
            ...(this._computedTargetWidth ? { width: `${this._computedTargetWidth}px` } : {})
          }
        : {}
    );
    return html`
      <div part="dropdown" class="${classes}" style=${inlineStyles}>
        ${this.selectall && this.multiselect
          ? html`<div class="select-all-button">
              <p>Select : <a @click=${() => this.select(this.options)}>All</a> | <a @click=${this.reset}>None</a></p>
            </div>`
          : nothing}
        ${this.searchindropdown ? this.#searchInputTemplate : nothing}
        ${this._isLoading ? html`<omni-loading-indicator></omni-loading-indicator>` : nothing}
        ${this._filteredOptions?.length && !this._isLoading
          ? html` <omni-options
              ?hasgroupheaders=${this.options.some(one => (one as GroupHeader).groupheader)}
              .options=${this._filteredOptions}
              .multiselect=${this.multiselect}
              .disabledList=${new Set([...this._disabled, ...this._disabledOnMaxSet])}
              .disabledMessage=${this.disabledMessage}
              .selected=${this._selected}
              .optionsPostTemplate=${this.optionsPostTemplate}
              @selectChange=${this.#handleSelectChange}
              @enterOnItem=${this.#handleEnterOnItem}></omni-options>`
          : nothing}
        ${!this._filteredOptions?.length && !this._isLoading && this.dynamicadditions && this._searchTerm
          ? html`<div class="item">
              <p class="add-dynamic" @click=${this.#handleDynamicAddition}>add ${this._searchTerm} as option</p>
            </div>`
          : nothing}
        ${!this._filteredOptions?.length && !this._isLoading && !this.dynamicadditions && this._searchTerm
          ? html`<div class="item disabled">
              <p class="no-results">No results</p>
            </div>`
          : nothing}
      </div>
    `;
  }

  get #footerTemplate() {
    if (this.hidefooter) return nothing;
    const footerSpacer = html`<div class="footer-spacer"></div>`;
    if (!this.error && !this.helpText) return footerSpacer;
    if (this.error) {
      return html` <div class="footer-container error-text">
        <omni-icon icon-id="omni:informative:error"></omni-icon>
        <span>${this.error}</span>
      </div>`;
    }
    if (this.helpText) {
      return html`
        <div class="footer-container help-text">
          <span>${this.helpText}</span>
        </div>
      `;
    }
  }

  #shouldPreventLastDeselect(option: Option): boolean {
    if (this.preventLastRemoval && this._selected.has(option)) {
      if (this.multiselect && this._selected.size === 1) {
        return true;
      }
      if (!this.multiselect) {
        this.blur();
        this.opened = false;
        return true;
      }
    }
    return false;
  }

  /**
   * - logic for selecting single, multi, noselct, and disabled options.
   * - handles form value and validity
   * - TODO: refactor for simplicity and handle revalidating on min/max changes after init */
  #commitSelections(options: Option[], shouldSelect: boolean = null, skipEmit: boolean = false) {
    if (!options.length) return;
    for (const option of options) {
      if (this._disabled.has(option)) continue;
      if (this.#shouldPreventLastDeselect(option)) continue;
      const existsInSelected = shouldSelect ?? this._selected.has(option);
      this._internals.setValidity({});
      const logicHandler = {
        singleselect: () => {
          this._selected.clear();
          if (!existsInSelected) {
            this._selected.add(option);
            this._internals.setFormValue(this.#selectedToFormDataString);
          } else {
            this._internals.setFormValue('');
            if (this.required) {
              this._internals.setValidity({ valueMissing: true }, this.valueMissingMessage, this.targetEl);
            }
          }
          this.opened = false;
        },
        multiselect: () => {
          if (!existsInSelected) {
            this._selected.add(option);
            this._internals.setFormValue(this.#selectedToFormDataString);
          } else {
            this._selected.delete(option);
            this._internals.setFormValue(this.#selectedToFormDataString);
          }
          // required and min and max not set
          if (this.required && this._selected.size < 1 && !this.min && !this.max) {
            this._internals.setValidity({ valueMissing: true }, this.valueMissingMessage, this.targetEl);
          }
          // only min is set
          if (this.min && !this.max && this._selected.size < this.min) {
            this._internals.setValidity(
              { rangeUnderflow: true },
              `Must make at least ${this.min} selections.`,
              this.targetEl
            );
          }
          // only max is set
          if (!this.min && this.max && this._selected.size > this.max) {
            this._internals.setValidity(
              { rangeOverflow: true },
              `Only ${this.max} maximum selections allowed.`,
              this.targetEl
            );
          }
          // min and max are set
          if (this.min && this.max && (this._selected.size < this.min || this._selected.size > this.max)) {
            this._internals.setValidity(
              { customError: true },
              `Must make at least ${this.min} minimum and ${this.max} maximum selections.`,
              this.targetEl
            );
          }
        }
      };
      logicHandler[this.multiselect ? 'multiselect' : 'singleselect']();
    }
    if (this.valid) {
      this.error = '';
    }
    this._selected = new Set(this._selected);
    this.#updateReflectedValue();
    if (!skipEmit) {
      this.#emitChange();
    }
    if (this.noselect) {
      this._selected = new Set();
      this.#updateReflectedValue();
    }
  }

  #updateReflectedValue() {
    this.value = [...this._selected.values()];
  }

  /** select items in dropdown */
  public select(options: Option[]) {
    this.#commitSelections(options, false);
  }

  /** unselect items in dropdown */
  public unselect(options: Option[]) {
    this.#commitSelections(options, true);
  }

  /** toggle items to be selected or unselected based on current state */
  public toggle(options: Option[]) {
    this.#commitSelections(options);
  }

  /** add options to the list of disabled options */
  public disableOptions(options: Option[]) {
    options.forEach(option => {
      this._disabled.add(option);
    });
  }

  /** add options to the list to remove it from disabled options , NOTE: if using object as option
   * then either use disabled property or public method, Using both can cause unexpected behavior.
   */
  public enableOptions(options: Option[]) {
    options.forEach(option => {
      this._disabled.delete(option);
    });
  }

  /** reset all selected options, form value, and reflected value */
  public reset() {
    this._selected = new Set();
    this._disabled = new Set();
    // if the option is an object and disabled is set, keep it disabled
    this.options.forEach(option => {
      if (option && typeof option === 'object' && option.disabled) {
        this._disabled.add(option);
      }
    });
    this.#updateReflectedValue();
    if (this.required) {
      this._internals.setValidity({ valueMissing: true }, this.valueMissingMessage, this.targetEl);
    } else {
      this._internals.setValidity({});
    }
    this._internals.setFormValue('');
    this.#emitChange();
  }

  /** @internal */
  #conditionalLeftLabelWrapper(template: TemplateResult) {
    if (this.labelPosition === 'left') {
      return html` <div class="wrapper">${template}</div> `;
    } else {
      return template;
    }
  }

  //prettier-ignore
  render() {
    return html`
      <div class="container ${this.labelPosition}">
        ${this.#conditionalLeftLabelWrapper(html` 
          ${this.#labelTemplate} 
          ${this.#targetTemplate} 
        `)}
        ${this.#footerTemplate} 
        ${this.#dropdownTemplate}
      </div>
    `;
  }
}

OmniElement.register('omni-dropdown', OmniDropdown);

declare global {
  interface HTMLElementTagNameMap {
    'omni-dropdown': OmniDropdown;
  }
}
