import { html, nothing, type TemplateResult } from 'lit';
import { unsafeHTML } from 'lit-html/directives/unsafe-html.js';
import { property, state } from 'lit/decorators.js';
import DOMPurify from 'dompurify';
import { OmniElement } from '../../omni-element.js';

import {css} from 'lit';
const styles = css`:host{--aspect-ratio: 16/9}iframe,.skeleton{aspect-ratio:var(--aspect-ratio);max-height:100%}.skeleton{width:100%}`


type MediaType = '_youtube' | '_vimeo' | '_spotify' | '_soundcloud';

/**
 * A media player to embed specific sources via the [oEmbed](https://oembed.com/) specification.
 *
 * To include within your project `import { OmniMediaEmbed } from 'omni-ui';`
 *
 * **Content Security Policy and OmniMediaEmbed**
 *
 * The OmniMediaEmbed component uses the `<iframe>` tag to embed content from
 * YouTube, Vimeo, Open Spotify, and SoundCloud. If you are using a Content Security
 * Policy (CSP) in your application, you will need to add the following to your
 * CSP:
 * ```
 * connect-src https://www.youtube.com/oembed https://vimeo.com/api/oembed.json
 * https://soundcloud.com/oembed https://open.spotify.com/oembed;
 * frame-src https://www.youtube.com https://player.vimeo.com
 * https://open.spotify.com https://w.soundcloud.com;
 * ```
 *
 * **Rollup Configuration**
 *
 * You may encounter issues with DOMPurify when bundling code that imports OmniMediaEmbed.
 * Set exportConditions to `'module'` in your nodeResolve plugin to ensure that the
 * correct distribution of DOMPurify is used.
 *
 * In your rollup.config.mjs:
 *
 * ```js
 * {
 *   ...
 *   plugins: [
 *     nodeResolve({ exportConditions: ['module'] })
 *   ]
 * }
 * ```
 *
 * **HTTP Referrer-Policy**
 *
 * If you encounter issues with youtube embeds on a live server, add the following meta
 * tag to your index.html `<meta name="referrer" content="origin-when-cross-origin" />`
 *
 * @tagname omni-media-embed
 * @cssprop [--aspect-ratio=16/9] - specify the aspect ratio for the containing element host and skeleton
 */
export class OmniMediaEmbed extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  @state() private _mediaType: MediaType;
  @state() private _loading: boolean = true;
  @state() private _noContent: boolean = false;
  private _youtube: TemplateResult | typeof nothing;
  private _vimeo: TemplateResult | typeof nothing;
  private _soundcloud: TemplateResult | typeof nothing;
  private _spotify: TemplateResult | typeof nothing;
  /** a valid youtube, vimeo, spotify, or soundcloud url */
  @property({ type: String }) src: string;

  willUpdate(changed: Map<string, unknown>) {
    if (changed.has('src')) {
      if (this.src.includes('youtube') || this.src.includes('youtu.be')) {
        this._loading = true;
        this.#fetchEmbed('https://www.youtube.com/oembed', '_youtube');
        return;
      }

      if (this.src.includes('vimeo')) {
        this._loading = true;
        this.#fetchEmbed('https://vimeo.com/api/oembed.json', '_vimeo');
        return;
      }
      if (this.src.includes('spotify')) {
        this._loading = true;
        this.#fetchEmbed('https://open.spotify.com/oembed', '_spotify', false);
        return;
      }
      if (this.src.includes('soundcloud')) {
        this._loading = true;
        this.#fetchEmbed('https://soundcloud.com/oembed', '_soundcloud');
        return;
      }
      this._noContent = true;
    }
  }

  #fetchEmbed(oembedUrl: string, mediaType: MediaType, replaceHeight: boolean = true) {
    fetch(`${oembedUrl}?url=${encodeURIComponent(this.src)}`)
      .then(resp => resp.json())
      .then(resp => {
        let snippet = resp.html.replace(/width="\d+"/, 'width="100%"');
        if (replaceHeight) {
          snippet = snippet.replace(/height="\d+"/, '');
        }
        const cleanSnippet = DOMPurify.sanitize(snippet, {
          ADD_TAGS: ['iframe'],
          ADD_ATTR: ['allow', 'allowfullscreen', 'frameborder', 'scrolling']
        });
        this[mediaType] = html`${unsafeHTML(cleanSnippet)}`;
        this._mediaType = mediaType;
        this._loading = false;
      })
      .catch(() => {
        this[mediaType] = nothing;
        this._mediaType = mediaType;
        this._loading = false;
      });
  }

  render() {
    if (this._noContent) return nothing;
    return !this._loading
      ? this[this._mediaType] ?? nothing
      : html`<omni-style><div class="skeleton"></div></omni-style>`;
  }
}

OmniElement.register('omni-media-embed', OmniMediaEmbed);
