import { html } from 'lit';
import { property, state } from 'lit/decorators.js';
import { ElementInternalsBase } from '../../element-internals-base.js';
import { OmniElement } from '../../omni-element.js';
import { OmniRadio } from '../omni-radio/omni-radio.js';

OmniRadio.register();

/**
 * To include within your project `import { OmniRadioGroup } from 'omni-ui';`
 *
 * @tagname omni-radio-group
 */
export class OmniRadioGroup extends ElementInternalsBase<string> {
  static get styles() {
    return [...super.styles];
  }

  @property({ type: String }) valueMissingMessage = 'Please select a radio if you want to proceed.';
  @property({ type: String, reflect: true }) name: string;
  @property({ type: String, reflect: true }) value: string;

  @state() private radios: OmniRadio[] = [];

  #handleChange(e: CustomEvent<boolean>) {
    const theChangedRadio = e.target as OmniRadio;
    // if radio is going from checked to unchecked, recheck it and return
    if (!theChangedRadio.checked) {
      theChangedRadio.checked = true;
      // stop the current event, and create a new event so upper event listeners are not confused by e.detail
      e.stopPropagation();
      this.dispatchNewEvent('change', { detail: true }, theChangedRadio);
      return;
    }
    // if radio is going from unchecked to checked, uncheck all other radios
    this.radios.forEach(oneRadio => {
      if (oneRadio !== theChangedRadio) {
        oneRadio.checked = false;
      }
    });
    // then set the value of the radio group to the value of the checked radio
    this.value = theChangedRadio.value;
    this.#determineValidityAndValue();
    // the @change event from the omni-radio will bubble up to the omni-radio-group so no need to emit a new event here
  }

  #handleSlotChange(e: Event & { target: HTMLSlotElement }) {
    this.radios = e.target.assignedElements() as OmniRadio[];
    this.radios.forEach(oneRadio => {
      if (oneRadio.value === this.value) {
        oneRadio.checked = true;
      }
    });
    if (!this.radios.length) {
      throw new Error('omni-radio-group must have at least one omni-radio');
    }
    this.#determineValidityAndValue();
  }

  #determineValidityAndValue() {
    this._internals.setValidity({});
    if (!this.value && this.required) {
      this._internals.setValidity({ valueMissing: true }, this.valueMissingMessage);
    }
    this._internals.setFormValue(this.value);
  }

  render() {
    return html` <slot @change=${this.#handleChange} @slotchange=${this.#handleSlotChange}></slot> `;
  }
}
OmniElement.register('omni-radio-group', OmniRadioGroup);
