import DOMPurify from 'dompurify';
import { html } from 'lit';
import { property, state } from 'lit/decorators.js';
import { unsafeHTML } from 'lit/directives/unsafe-html.js';
import { marked } from 'marked';
import { OmniElement } from '../../omni-element.js';
import '../omni-style/omni-style.js';
import '../omni-tooltip/omni-tooltip.js';

import {css} from 'lit';
const styles = css`:host{font-size:var(--markdown-font-size, 1rem)}.omni p{padding-bottom:calc(1.65*var(--markdown-font-size, 1rem))}.omni p:last-child{padding-bottom:0}.omni .table td,.omni .table td{white-space:unset}.table-wrapper{max-width:100%;overflow-x:auto}.omni ol,.omni ul,.omni li{margin:revert;padding:revert;list-style:revert}`


// Add css class to table elements
marked.use({
  renderer: {
    table(header, body) {
      // eslint-disable-next-line no-param-reassign
      if (body) body = `<tbody>${body}</tbody>`;
      return `<div class="table-wrapper"><table class="table m-3"><thead>${header}</thead>${body}</table></div>\n`;
    },
    heading(text, level) {
      return `<h${level} class="is-size-${level} mb-5">${text}</h${level}>`;
    }
  }
});

/**
 * Safely render markdown to HTML
 *
 * This component safely renders markdown to HTML for display on the web. Markdown input is parsed by the
 * [`marked`](https://marked.js.org/) library and sanitized using [`DOMPurify`](https://github.com/cure53/DOMPurify).
 *
 * To include within your project `import { OmniMarkdown } from 'omni-ui';`
 *
 * ```html
 * <omni-style>
 *   <omni-markdown markdown="Hello _world_!"></omni-markdown>
 * </omni-style>
 * ```
 *
 * Markdown strings can be passed to the component as an attribute `markdown="Hello _world_!"` or property `.markdown=${sample}`.
 * Only the property will handle escaped characters correctly so if escaped characters are in your markdown, code blocks
 * for example, you will need to use the property variant.
 *
 * @tagname omni-markdown
 */
export class OmniMarkdown extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  /** markdown to render to HTML safely */
  @property({ type: String }) markdown: string;
  /** sanitized HTML string */
  @state() private rendered: string;

  willUpdate(changedProperties: Map<string, unknown>) {
    if (changedProperties.has('markdown') && this.markdown?.length > 0) {
      this.rendered = DOMPurify.sanitize(marked.parse(this.markdown));
    }
  }

  render() {
    return html` <omni-style> ${unsafeHTML(this.rendered ?? '')} </omni-style> `;
  }
}

OmniElement.register('omni-markdown', OmniMarkdown);

declare global {
  interface HTMLElementTagNameMap {
    'omni-markdown': OmniMarkdown;
  }
}
