import { html, nothing } from 'lit';
import { property, state, query } from 'lit/decorators.js';
import { OmniElement } from '../../omni-element.js';
import '../omni-progress-bar/omni-progress-bar.js';

import {css} from 'lit';
const styles = css`:host{overflow:visible;position:relative;z-index:999;top:0;bottom:0;left:0;right:0}.modal-container{display:flex;flex-direction:column;align-items:center;justify-content:center;position:fixed;top:0;bottom:0;left:0;right:0}section.backdrop{position:fixed;top:0;bottom:0;left:0;right:0;background-color:#3b3e3f;opacity:.5;z-index:1000;cursor:default}section.body{position:relative;z-index:1001;width:479px;padding:40px;border:1px solid #edf0f4;border-radius:10px;background-color:#fff;display:flex;flex-direction:column;justify-content:center}section.body span.step{font-size:14px;font-weight:600;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:.31px;color:#3b3e3f;margin-bottom:20px}section.body div.bars{display:flex;align-items:center;justify-content:space-between;margin-bottom:8px}section.body div.bars omni-progress-bar{flex-grow:1;margin:0 5px}section.body span.info{font-size:14px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:.31px;color:#a2a9ad;text-align:center}section.body ::slotted(div[slot=buttons]){margin-top:16px;display:flex;justify-content:end}`


export interface ProgressStep {
  text: string;
  ratio: number;
}

/**
 *
 * Progress bar with configurable phases
 *
 * To include within your project `import { OmniModalProgress } from 'omni-ui';`
 *
 * ## 📝 Usage
 *
 * **Basic Omni Progress Modal Rendering**:
 *
 * ```html
 * <omni-style>
 *  <omni-modal-progress
 *   .steps=${this.steps}
 *   .estimatedTime=${5}
 *   showcountdown>
 *  </omni-modal-progress>
 * </omni-style>
 * ```
 *
 * ```Js
 * // if the request completes before the estimated time, animate the progress bars to completed,
 * // then do an action like routing
 * async closeModalEarly(){
 *   const omniModalProgress = this.shadowRoot.querySelector('omni-modal-progress');
 *   await omniModalProgress.rollToComplete();
 *   router.navigate('/complete');
 * }
 * ```
 *
 * ```Js
 * // if needed, add a click event handler to close the modal from clicks
 * handleClickOutsideModalBody(e) {
 *   const omniModalProgress = this.shadowRoot.querySelector('omni-modal-progress');
 *   if (omniModalProgress) {
 *     const { modalBody } = omniModalProgress;
 *     const isInsideModal = e.composedPath().includes(modalBody);
 *     if (!isInsideModal) {
 *       this.showModal = false;
 *     }
 *   }
 * }
 * ```
 * **Slots**:
 *
 * `<omni-modal-progress>` has two slots:
 * - `message`: The message slot element
 * - `buttons`: the buttons slot element
 *
 * **Methods**:
 *
 * `<omni-modal-progress>` has two methods:
 * - `rollToComplete()`: call to animate the progress bars to complete returns a promise
 * @tagname omni-modal-progress
 * @slot message - The message slot element
 * @slot buttons - The buttons slot element
 */

export class OmniModalProgress extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  #stepInterval: ReturnType<typeof setInterval>;
  #elapsedInterval: ReturnType<typeof setInterval>;
  @state() private activeStepIdx: number = 0;
  @state() private elapsed: number = 0;
  @state() private progress: number[] = [];
  @state() private isRollingToComplete = false;
  @state() private slottedMessageExists = false;
  @property({ type: Number }) estimatedTime: number = 30;
  @property({ type: Array }) steps: ProgressStep[] = [];
  @property({ type: Boolean, attribute: true }) showcountdown = false;
  @query('.body') private modalBody!: HTMLElement;

  connectedCallback() {
    super.connectedCallback();
    this.progress = new Array(this.steps.length).fill(0);
    this.#elapsedInterval = setInterval(() => {
      this.elapsed = this.elapsed + 1;
      if (this.elapsed >= this.estimatedTime) {
        clearInterval(this.#elapsedInterval);
      }
    }, 1000);
    this.moveToStep(0);
  }

  private moveToStep(idx: number) {
    this.activeStepIdx = idx;
    const intervalStepPeriod = 100;
    const stepsToMake100 = this.estimatedTime * this.steps[idx].ratio * 100 * (intervalStepPeriod / 1000);
    this.#stepInterval = setInterval(() => {
      this.progress[idx] += intervalStepPeriod / stepsToMake100;
      this.requestUpdate();
      if (this.progress[idx] > 100) {
        clearInterval(this.#stepInterval);
        if (this.steps[idx + 1]) {
          this.moveToStep(idx + 1);
        }
      }
    }, intervalStepPeriod);
  }

  public async rollToComplete() {
    this.isRollingToComplete = true;
    clearInterval(this.#stepInterval);
    this.elapsed = this.estimatedTime - 1;
    const timeout = (timeoutMs: number) => new Promise(resolve => setTimeout(resolve, timeoutMs));
    for (const idx in this.steps) {
      if (this.progress[idx] < 100) {
        this.progress[idx] = 100;
        this.requestUpdate();
        await timeout(500);
      }
    }
    clearInterval(this.#elapsedInterval);
  }

  render() {
    const remaining = this.estimatedTime - this.elapsed;
    const countdown = this.showcountdown ? `About ${remaining} second${remaining > 1 ? 's' : ''} remaining.` : nothing;
    const countdownPadding = this.slottedMessageExists ? 'margin-top: 8px' : 'margin-top: 24px';
    return html`
      <omni-style>
        <div class="modal-container">
          <section class="backdrop"></section>
          <section class="body">
            <span class="step"> ${this.steps[this.activeStepIdx].text} </span>
            <div class="bars">
              ${this.steps.map(
                (step, idx) => html` <omni-progress-bar .value=${this.progress[idx]}></omni-progress-bar> `
              )}
            </div>
            <slot name="message" @slotchange=${() => (this.slottedMessageExists = true)}></slot>
            <span class="info" style="${countdownPadding}">
              ${remaining > 0
                ? countdown
                : !this.isRollingToComplete
                  ? 'This is taking longer than expected'
                  : countdown}
            </span>
            <slot name="buttons"></slot>
          </section>
        </div>
      </omni-style>
    `;
  }
}

OmniElement.register('omni-modal-progress', OmniModalProgress);

declare global {
  interface HTMLElementTagNameMap {
    'omni-modal-progress': OmniModalProgress;
  }
}
