/**
 * Optimized SVG sets that load dynamically
 *
 * To include within your project `import { OmniIcon } from 'omni-ui';`
 *
 * @tagname omni-icon
 * @cssprop [--color-icon-lines=#3b3e3f] override icon fill color
 */
export class OmniIcon extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  /** aria-label */
  @property({ type: String, attribute: 'aria-label', reflect: true }) ariaLabel: string = 'icon';
  /** icon-id for omni-icon */
  @property({ type: String, attribute: 'icon-id', reflect: true }) iconId = '';
  // eslint-disable-next-line lit/no-native-attributes
  @property({ type: String, reflect: true }) role: string = 'img';
  /**@internal */
  icon: TemplateResult;
  /**@internal */
  resolver: (group: string) => Promise<TemplateResult>;

  update(changedProperties) {
    super.update(changedProperties);
    if (changedProperties.has('iconId')) {
      this._updateIcon();
    }
  }

  /**@internal */
  _updateIcon(resolve = this.resolver ?? resolveOmniIconset) {
    const iconParts = (this.iconId || '').trim().split(':');
    const [collection, group, id] = iconParts;

    if (!this.iconId) {
      console.warn(`<omni-icon>: icon-id is not set`);
      return;
    }

    return resolve(group)
      .then(res => {
        const icon = res[id]();
        this.icon = icon;
        return { res, collection, group, id, icon };
      })
      .catch(error => {
        console.error(`<omni-icon>: Error resolving icon-id ${this.iconId}`, error);
        this.icon = svg`?`;
        return error;
      })
      .finally(() => {
        this.requestUpdate();
      });
  }

  render() {
    return html`<div part="icon">${this.icon ?? ''}</div>`;
  }
}

OmniElement.register('omni-icon', OmniIcon);

declare global {
  interface HTMLElementTagNameMap {
    'omni-icon': OmniIcon;
  }
}