/*
 * Allows to use context Provider and Consumer components in a similar manner than the Context in React. This is a direct derivative of a customized fork of content-provider.
 * @see {@link https://github.com/pinkhominid/context-provider} fork of context-provider.
 * @see {@link https://github.com/kcmr/context-provider} context-provider.
 *
 * */
const providerSymbol = Symbol('provider') as unknown as string;
const contextSymbol = Symbol('context') as unknown as string;
const EVENT_TYPE = 'context-provider';

/**
 * Gets the Provider component that matches the identifier.
 *
 * @param {*} identifier Provider identifier.
 * @param {HTMLElement} context
 */
const getProvider = async (identifier, context) => {
  const eventInit: CustomEventInit = { bubbles: true, composed: true, detail: { identifier } };
  const event = new CustomEvent<{ provider?: unknown }>(EVENT_TYPE, eventInit);
  context.dispatchEvent(event);

  if (!event.detail.provider) {
    // If both consumer and provider's connectedCallback() fire in the same call
    // stack, provider might not exist yet. Wait for current call stack to finish &
    // try again.
    await new Promise(res => setTimeout(res, 0));
    context.dispatchEvent(event);
  }
  return event.detail.provider;
};

/**
 * Provides a `context` property with the value of the nearest Provider component.
 * Changes in the `value` property of the Provider can be handled by implementing
 * the `onContextChanged()` hook.
 *
 * @mixin
 */
const contextConsumerMixin = SuperClass =>
  class Consumer extends SuperClass {
    constructor() {
      super();
      this.onContextChanged = this.onContextChanged.bind(this);
    }

    get context() {
      if (this[providerSymbol]) {
        return this[providerSymbol].value;
      }

      return undefined;
    }

    async _discoverContextProvider() {
      this._forgetContextProvider();
      this[providerSymbol] = await getProvider(contextSymbol, this);

      if (this[providerSymbol]) {
        this[providerSymbol].addEventListener('context-changed', this.onContextChanged);
      }

      // If context is already defined in the provider when the mixin component is
      // created/connected we should consider that a change (from undefined), otherwise
      // the component may never find out when the context is available.
      if (this.context !== undefined) {
        // @ts-ignore
        this.onContextChanged(new Event('context-changed'));
      }
    }

    _forgetContextProvider() {
      // @ts-ignore
      if (this[providerSymbol]) {
        // @ts-ignore
        this[providerSymbol].removeEventListener('context-changed', this.onContextChanged);
      }
      // @ts-ignore
      this[providerSymbol] = null;
    }

    connectedCallback() {
      if (super.connectedCallback) {
        super.connectedCallback();
      }

      this._discoverContextProvider();
    }

    disconnectedCallback() {
      this._forgetContextProvider();

      if (super.disconnectedCallback) {
        super.disconnectedCallback();
      }
    }

    /**
     * Callback executed when the nearest Provider changes its `value` property.
     */
    // eslint-disable-next-line class-methods-use-this, @typescript-eslint/no-empty-function
    onContextChanged() {}
  };

export { EVENT_TYPE, providerSymbol, contextSymbol, contextConsumerMixin };
