import 'element-internals-polyfill'; // 'https://unpkg.com/element-internals-polyfill';
import { css } from 'lit';
import { OmniElement } from './omni-element.js';
import { OmniOverlay } from './component/omni-overlay/omni-overlay.js';

export { css };
/*
    WIP OmniFormControl provides features common to most/all form elements WIP

    @description 
    form elements have to render in the same non-shadow/light-dom tree as the form
    the containing form can be in a shadowRoot, use slots as needed

    TODO form validation and related, Constraint Validation APIs... ValidityState (linked below)
    TODO form-container events: reset, submit
    TODO element events: invalid (no-bubbling), input (bubbles)
    TODO attributes disabled required ...
    TODO focus, blur

    prior art: omni-form-control follows existing HTML API patterns, especially native form elements,
    emulating these so the components have less deviation from native equivalanets and the knowledge
    is transferrable;

    current art: look and feel from UI+UX reconciled for the personas defined with accessibility and 
    related (government) requirements combined to meet the various practical and system considerations

    @see {@link https://html.spec.whatwg.org/multipage/custom-elements.html#element-internals} ElementInternals, Custom Elements
    @see {@link https://developer.mozilla.org/docs/Web/Accessibility/ARIA/Roles} Aria roles
    @see {@link https://developer.mozilla.org/docs/Web/Accessibility/ARIA} Aria overview
    @see {@link https://developer.mozilla.org/docs/Web/HTML/Attributes} attributes reference
    @see {@link https://designsystem.digital.gov/components/overview/} US WDS components with related info
    @see {@link https://designsystem.digital.gov/templates/form-templates/} US WDS Form templates patterns
    @see {@link https://developer.mozilla.org/docs/Web/API/Constraint_validation} Constraint Validation API
    @see {@link https://developer.mozilla.org/docs/Web/Guide/HTML/Constraint_validation} Constraint validation guide
    @see {@link https://developer.mozilla.org/docs/Web/API/ValidityState} ValidityState API
    @see {@link https://developer.mozilla.org/docs/Learn/Forms} ensure compatibility working with forms

 */
export class OmniFormControl extends OmniElement {
  // value implement in component
  static get properties() {
    return {
      autofocus: { type: Boolean, reflect: true },
      autocomplete: { type: Boolean, reflect: true },
      disabled: { type: Boolean, reflect: true },
      required: { type: Boolean, reflect: true },
      name: {
        type: String,
        reflect: true,
        converter: {
          // when the name is empty remove the attribute
          toAttribute(value) {
            const name = value.trim();
            return name || undefined;
          }
        }
      }
    };
  }

  static get styles() {
    return [
      css`
        :host {
          --bg-color: var(--color-pale-grey-two, #fff);
          background-color: var(--bg-color);
          border: var(--form-input-border, #ddd);
          --color: var(--color-gray-36);
          --color-disabled: #aaa;
        }
        :host([disabled]) {
        }
      `
    ];
  }

  connectedCallback() {
    super.connectedCallback();
    // initialize the overlay whenever any form element is connected
    OmniOverlay.overlay;
    if (!this.hasAttribute('tabindex')) {
      // tabIndex required for focusable form controls; most values are fine, even -1
      this.tabIndex = 0;
    }
  }

  static get formAssociated() {
    return true;
  }

  constructor() {
    super();
    this.name = '';
    this.required = false;
    this.disabled = false;

    // TODO for Safari https://github.com/calebdwilliams/element-internals-polyfill
    this.internals = this.attachInternals();
  }

  get form() {
    return this.internals.form;
  }

  get labels() {
    return this.internals.labels;
  }

  get type() {
    return this.localName;
  }

  get validity() {
    return this.internals.validity;
  }

  get validationMessage() {
    return this.internals.validationMessage;
  }

  get willValidate() {
    return this.internals.willValidate;
  }

  setCustomValidity(msg) {
    msg ? this.internals.setValidity({ customError: true }, msg) : this.internals.setValidity({});
    return this.validationMessage;
  }

  /*
  TODO implement validity checks for the attributes in the link
  static validation
  @return {Boolean} true ok, false fail
  @see {@link https://developer.mozilla.org/docs/Web/Guide/HTML/Constraint_validation} Constraint_validation API
   */
  checkValidity() {
    return this.internals.checkValidity();
  }

  /*
    interactive validation, ie shows ui elements related to invalid
    TODO
   */
  reportValidity() {
    return this.internals.reportValidity();
  }

  /** setup in components if indicated
	  formAssociatedCallback(form){ }
	  formResetCallback(){ }
	  // called when ancestor fieldset and similar are [disabled], and the element itself is NOT [disabled]
	  formDisabledCallback(disabled){ }
	  // mode === 'restore' when browser restarts, navigation; or alternately 'autocomplete' where enabled on the form or element
	  formStateRestoreCallback(state, mode){}
  */
  formStateRestoreCallback(state, mode) {
    console.warn('formStateRestoreCallback', mode, state);
  }
}
