import { type CSSResult, html } from 'lit';
import { property } from 'lit/decorators.js';
import { OmniElement } from '../../omni-element.js';
import { OmniStyle } from '../omni-style/omni-style.js';
import { OmniIcon } from '../omni-icon/omni-icon.js';
import { OmniTooltip } from '../omni-tooltip/omni-tooltip.js';
import { classMap } from 'lit/directives/class-map.js';

import {css} from 'lit';
const styles = css`:host([disabled]:hover),:host([disabled]){cursor:not-allowed}.omni .button.pd{padding:12px 18px}.omni .button.pd.is-active{background-image:rgb(var(--rgb-primary), 0.15);border:var(--color-transparent);color:var(--color-core-light)}`


OmniStyle.register();
OmniIcon.register();
OmniTooltip.register();

type Placement = 'top' | 'bottom' | 'left' | 'right';

/**
 * Icon button with informative tooltip
 *
 * To include within your project `import { OmniIconActions } from 'omni-ui';`
 *
 * ```html
 * <omni-icon-action
 *   icon-id="omni:interactive:actions"
 *   message="Sample Message">
 * </omni-icon-action>
 * ```
 *
 * @tagname omni-icon-action
 */

export class OmniIconActions extends OmniElement {
  static get styles(): CSSResult[] {
    return [...super.styles, styles];
  }

  /** icon id for icon action */
  @property({ type: String, reflect: true, attribute: 'icon-id' }) iconId: string = '';
  /** message for tooltip  */
  @property({ type: String, reflect: true }) message: string = '';
  /** active state for icon-action */
  @property({ type: Boolean, reflect: true, attribute: true }) active: boolean = false;
  /** disables the icon action without tooltip text visible */
  @property({ type: Boolean, attribute: true }) disabled: boolean = false;
  /** shows tooltip text even when disabled */
  @property({ type: Boolean, attribute: 'show-on-disable' }) showOnDisable: boolean = false;
  /** position for tooltip content */
  @property({ type: String, attribute: true, reflect: true }) place: Placement = 'top';

  /** @internal */
  get iconActionTemplate() {
    const classes = classMap({
      'is-text': !this.active,
      'is-active': this.active,
      'is-shadowless': true,
      button: true,
      icon: true,
      pd: true
    });

    return this.message !== ''
      ? html`
          <omni-tooltip ?show-on-disabled=${this.showOnDisable} .place=${this.place}>
            <button slot="invoker" class=${classes} ?disabled=${this.disabled} part="icon-action-button">
              <omni-icon icon-id=${this.iconId}></omni-icon>
            </button>
            <div slot="content">
              <p>${this.message}</p>
            </div>
          </omni-tooltip>
        `
      : html`
          <button class=${classes} ?disabled=${this.disabled} part="icon-action-button">
            <omni-icon icon-id=${this.iconId}></omni-icon>
          </button>
        `;
  }

  render() {
    return html` <omni-style> ${this.iconActionTemplate} </omni-style> `;
  }
}

OmniElement.register('omni-icon-action', OmniIconActions);

declare global {
  interface HTMLElementTagNameMap {
    'omni-icon-action': OmniIconActions;
  }
}
