import { html } from 'lit';
import { property, query, state } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { OmniElement } from '../../omni-element.js';

import {css} from 'lit';
const styles = css`:host{--left: 100px;--top: 100px;--z-index: 1000}dialog{border:none;border-radius:5px;padding:1rem}dialog[non-modal]{margin:0;border:none;border-radius:5px;z-index:var(--z-index);box-shadow:0 0 15px rgba(0,0,0,.1);position:fixed;left:var(--left);top:var(--top)}dialog::backdrop{background-color:rgba(0,0,0,.4)}dialog[cursor-pointer]::backdrop{background-color:rgba(0,0,0,.4)}header.has-header{border-bottom:1px solid #e9ecef;margin:0 -1rem;padding:0 1rem 1rem}`


/**
 * To include within your project `import 'omni-ui/omni-modal.js';`
 *
 * @tagname omni-modal
 * @cssprop [--left=100px] - left position of the non-modal dialog
 * @cssprop [--top=100px] - top position of the non-modal dialog
 * @cssprop [--z-index=1000] - z-index of the non-modal dialog
 * @slot header - header slot
 * @slot default - body slot
 * @slot footer - footer slot
 * @event {} cancel - fires event when clicks on cancel
 */
export class OmniModal extends OmniElement {
  static get styles() {
    return [...super.styles, styles];
  }

  @state() private _hasHeader: boolean = false;
  @state() private _isModal: boolean = false;
  @query('dialog') private _dialogEl: HTMLDialogElement;
  @property({ type: Boolean, reflect: true }) initialFocus: boolean = false;

  public show() {
    this._isModal = false;
    this._dialogEl.show();
    if (this.initialFocus) {
      this._dialogEl.focus();
    }
  }

  public showModal() {
    this._isModal = true;
    this._dialogEl.showModal();
    if (this.initialFocus) {
      this._dialogEl.focus();
    }
  }

  public close() {
    this.dispatchNewEvent('cancel');
    return this._dialogEl.close();
  }

  #handleHeaderSlotChange(e: Event & { target: HTMLSlotElement }) {
    this._hasHeader = e.target.assignedElements({ flatten: true }).length > 0;
  }

  render() {
    return html`
      <dialog part="dialog" ?non-modal=${!this._isModal}>
        <header class=${classMap({ 'has-header': this._hasHeader })} part="header">
          <slot name="header" @slotchange=${this.#handleHeaderSlotChange}></slot>
        </header>
        <slot></slot>
        <slot name="footer"></slot>
      </dialog>
    `;
  }
}

OmniElement.register('omni-modal', OmniModal);

declare global {
  interface HTMLElementTagNameMap {
    'omni-modal': OmniModal;
  }
}
