import { OmniElement } from '../../omni-element.js';
import { OmniStyle } from '../omni-style/omni-style.js';

/**
 * To include within your project `import { OmniTheme } from 'omni-ui';`
 *
 * Change the default `omni-style` theme <br>
 * by adjusting stylesheet css properties intended for theming
 *
 * ```js
 * // in a component class: dispatch a custom event with detail of key-value pairs of css props to update (can be multiple at once)
 * this.dispatchNewEvent('omni-theme', {detail: {'--rgb-primary': '0, 161, 210', '--rgb-secondary': '3, 187, 227'}})
 *
 * // at the top level in <body>, add an empty omni-theme
 * // this will catch the event and update the css-props
 * html`
 *  <omni-theme></omni-theme>
 * `
 * ```
 *
 * @tagname omni-theme
 */
export class OmniTheme extends OmniStyle {
  static get defaultSelector() {
    return '.omni';
  }

  constructor() {
    super();
    this._themeListener = this._themeListener.bind(this);
  }

  get selector() {
    return (this.getAttribute('selector') || '').trim();
  }

  // @ts-ignore
  set selector(cssSelector = '') {
    // @ts-ignore
    return cssSelector.trim() ? this.setAttribute('selector', cssSelector) : this.removeAttribute('selector');
  }

  /* 
      @return {return} the theme-specific stylesheet that's inherited everywhere (document and shadowRoots) via omni-style
   */
  get styleSheet() {
    const { adoptedStyleSheets = [] } = this.ownerDocument;
    // assumptions about how this works TODO
    return adoptedStyleSheets[0] ?? null;
  }

  _formatRule([property, value]) {
    const name = this[property] ?? property;
    return `${name}:${value} !important;`;
  }

  _filterProperties([property]) {
    return property.startsWith('--');
  }

  _themeListener(event) {
    const { detail } = event;
    requestAnimationFrame(() => {
      const { selector, styleSheet } = this;
      const selectorMark = this.nodeName.toLowerCase() + (selector ? `[selector="${selector}"]` : '');
      const { rules } = styleSheet;
      for (let rule, i = rules.length; i--; i >= 0) {
        rule = rules[i];
        if (rule.selectorText?.includes(selectorMark)) {
          styleSheet.deleteRule(i);
          break;
        }
      }

      /** optional translate dictionary
             * event.detail
             {
                translate: {'--other-system-name': '--omniui-actual-name'},
                '--other-system-name':'#example-value'
                '--name':'#value'
                ...
             }
             */
      const { translate = {} } = detail;

      const cssRule = `${selectorMark}, ${selector || OmniTheme.defaultSelector}{${Object.entries(detail)
        // @ts-ignore
        .filter(this._filterProperties)
        .map(this._formatRule, translate)
        .join(' ')}}`;
      styleSheet.insertRule(cssRule, styleSheet.rules.length);
    });
  }

  connectedCallback() {
    super.connectedCallback();
    self.addEventListener('omni-theme', this._themeListener);
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    self.removeEventListener('omni-theme', this._themeListener);
  }
}

OmniElement.register('omni-theme', OmniTheme);

declare global {
  interface HTMLElementTagNameMap {
    'omni-theme': OmniTheme;
  }
}
