/* eslint-disable @typescript-eslint/no-unsafe-call */
/* eslint-disable @typescript-eslint/no-unsafe-return */
/* eslint-disable @typescript-eslint/no-unsafe-assignment */
/* eslint-disable wc/no-method-prefixed-with-on */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unsafe-member-access */
import { EVENT_TYPE, contextConsumerMixin, contextSymbol, providerSymbol } from './context-consumer.js';

const store = new WeakMap();

/**
 * Creates a Provider component that notifies changes on its
 * `value` property.
 *
 * @param {*} initialValue Initial value of the Provider
 * @param {*} identifier Provider ID
 */
const createProvider = (initialValue, identifier) =>
  class Provider extends HTMLElement {
    constructor() {
      super();
      this.value = initialValue;
      this[providerSymbol] = identifier;
      this.onGetProvider = this.onGetProvider.bind(this);
    }

    set value(value) {
      if (this.value !== value) {
        store.set(this, value);
        this.dispatchEvent(new Event('context-changed'));
      }
    }

    get value() {
      return store.get(this);
    }

    connectedCallback() {
      // @ts-expect-error - unknown super
      if (super.connectedCallback) {
        // @ts-expect-error - unknown super
        super.connectedCallback();
      }

      this.addEventListener(EVENT_TYPE, this.onGetProvider);
    }

    disconnectedCallback() {
      this.removeEventListener(EVENT_TYPE, this.onGetProvider);
      // @ts-expect-error - unknown super
      if (super.disconnectedCallback) {
        // @ts-expect-error - unknown super
        super.disconnectedCallback();
      }
    }

    onGetProvider(e) {
      if (!e.detail?.identifier) return;
      if (this[providerSymbol] !== e.detail.identifier) return;
      e.stopPropagation();
      e.detail.provider = this;
    }
  } as any;

/**
 * Creates a Provider class.
 * @param {*} initialValue Initial value of the context
 */

const createContextProvider = initialValue => createProvider(initialValue, contextSymbol);

export { contextConsumerMixin, createContextProvider, createProvider };
