/* icons have an iconId like 'collection:group:id' eg 'omni:app:warning' which was previously used with lion-icons in complex function caching instead of letting the runtime, http caches or svg elements handle this;
 */
export const omniIconsetTypes = ['interactive', 'informative', 'object', 'brand', 'app', 'editor'];

export async function resolveOmniIconset(group = '?') {
  // TODO NOTE: don't turn switch into a dynamic import path or rollup won't process it!
  // e.g. return await import(`./iconset-${group}.js`);
  switch (group) {
    case 'interactive':
      return import(`./iconset-interactive.js`);
    case 'informative':
      return import(`./iconset-informative.js`);
    case 'object':
      return import(`./iconset-object.js`);
    case 'brand':
      return import(`./iconset-brand.js`);
    case 'app':
      return import('./iconset-app.js');
    case 'editor':
      return import('./iconset-editor.js');
    default:
      throw new Error(`Unknown icon set "${group}"`);
    // no default
  }
}

export const resolveOmniIcon = resolveOmniIconset;
